import React, { useEffect, useState } from 'react'
import './MintSection.scss';
import { useDispatch, useSelector } from "react-redux";
import { connect } from "../../redux/blockchain/blockchainActions";
import { fetchData } from "../../redux/data/dataActions";
import { toast } from 'react-toastify';
import loading from '../../assets/images/loading.svg'
import whiteListAddresses from '../../addresses.json'
import MerkleTree from 'merkletreejs'
import keccak256 from 'keccak256'
import Web3EthContract from "web3-eth-contract";

import Cube from '../cube/Cube'



function MintSection() {



  const truncate = (input, len) =>
     input.length > len ? `${input.substring(0, len)}...` : input;
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const [claimingNft, setClaimingNft] = useState(false);
  const [mintAmount, setMintAmount] = useState(1);
  const [paused,setPaused] = useState('')
  const [cost,setCost] = useState('')
  const [maxPerWallet,setMaxPerWallet]=useState('');
  const[limit,setLimit]=useState('')
  const[totalSupply,setTotalSupply]=useState('')
  const[refresh,setRefresh]=useState(0)
  const[ethereum,setEthereum]=useState(null)
  const[balanceExceeded,setBalanceExceeded]=useState('')
  const[balance,setBalance]=useState('')
  const[addressWhitelisted,setAddressWhitelisted]=useState('')


  const[publicActive,setPublicActive]=useState(false)
  const[presaleActive,setPresaleActive]=useState(false)

  const CONFIG= {
    "CONTRACT_ADDRESS": "0x1d9B317B401003E4881D37301ae5DcFa4718c26e",
    "SCAN_LINK": "https://etherscan.io/address/0x1d9B317B401003E4881D37301ae5DcFa4718c26e",
    "NETWORK": {
      "NAME": "Ethereum",
      "SYMBOL": "ETH",
      "ID":1
    },
    "MAX_SUPPLY": 10000,
    "WEI_COST": 50000000000000000,
    "DISPLAY_COST": 0.0777,
    "GAS_LIMIT": 130000,
    "MARKETPLACE_LINK": "",
  }
  

  const claimNFTs = () => {
    if(paused===true){
      toast.error('Mint coming soon')
      return;
    }
    if(claimingNft||cost==='')return;
    
    let cost2 = cost;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);


    const whiteListAddressesLeaves = whiteListAddresses.map((x) => keccak256(x))
    const merkletree = new MerkleTree(whiteListAddressesLeaves, keccak256 ,{sortPairs:true})
    const merklePoof=merkletree.getHexProof(keccak256(blockchain.account))

    toast.success(`Minting your ${mintAmount > 1 ? 'LATCs' : 'LATC'}...`);
    setClaimingNft(true);
    blockchain.smartContract.methods
      .mint(merklePoof ,mintAmount)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
        value: totalCostWei,
      })
      .once("error", (err) => {
        console.log(err);
        toast.error("Sorry, something went wrong please try again later.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        toast.success(() =>
          <span>
            C.U.B. Mint Successful! Visit <a href={CONFIG.MARKETPLACE_LINK}>opensea.io</a> to view it.
          </span>
        );
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
        setRefresh(refresh+1)
        setMintAmount(1)
      });
  };


  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  // const getConfig = async () => {
  //   const configResponse = await fetch("/config/config.json", {
  //     headers: {
  //       "Content-Type": "application/json",
  //       Accept: "application/json",
  //     },
  //   });
  //   const config = await configResponse.json();
  //   SET_CONFIG(config);
  // };

  // useEffect(() => {
  //   getConfig();

  // }, []);

  useEffect(() => {
    getData();

  }, [blockchain.account]);

  const mint = () => {
    claimNFTs();
    getData();
  }

  const connectWallet=()=>{
    // if(paused===''&&ethereum===null)return;
    dispatch(connect());
    getData();
  }

  const initConnect=async()=>{
    const { ethereum } = window;
    Web3EthContract.setProvider(ethereum);
    const address=CONFIG.CONTRACT_ADDRESS
    const abiResponse = await fetch("/config/abi.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const abi = await abiResponse.json();

    const SmartContractObj = new Web3EthContract(
      abi,
      address
    );

    ethereum.on("accountsChanged", (accounts) => {
      setRefresh(refresh+1)
    });
    
    if(ethereum){
      ethereum.on("chainChanged", () => {
        window.location.reload();
      });
  
      try{
        const presaleCost=await SmartContractObj.methods.presaleCost().call();

        const publicSaleCost=await SmartContractObj.methods.publicSaleCost().call();
        const maxPerPresaleWallet=await SmartContractObj.methods.maxMintAmountPerPresaleAccount().call()

        const maxPerPublicSaleWallet=await SmartContractObj.methods.maxMintAmountPerPublicAccount().call()
        const presaleActive=await SmartContractObj.methods.presaleActive().call()
  
        const paused=await SmartContractObj.methods.paused().call();
        const totalSupply=await SmartContractObj.methods.totalSupply().call();
  
        if(presaleActive){
          setCost(presaleCost)
          setMaxPerWallet(maxPerPresaleWallet)
          setPresaleActive(true)
        }
        else {
          setCost(publicSaleCost)
          setMaxPerWallet(maxPerPublicSaleWallet)
          setAddressWhitelisted(true)
          setPublicActive(true)
        }
      
        setTotalSupply(Number(totalSupply))
      
        setPaused(paused)
    
      }
      catch(err){
        console.log(err)
      }
      
      setEthereum(ethereum)
    }
    else{
      toast.error('Install metamask!')
    }
   
  }

  useEffect(()=>{
    if(CONFIG.CONTRACT_ADDRESS==='')return;

    initConnect()

  },[refresh,CONFIG])


  useEffect(()=>{
    if(blockchain.account===null||maxPerWallet==='')return;

    blockchain.smartContract.methods.balanceOf(blockchain.account).call().then((result)=>{
      let balance=Number(result);
      setBalance(Number(result));
      console.log(balance)
      let limit=Number(maxPerWallet)-balance;
      console.log(limit)
      if(limit<=0){
        setLimit(0)

      }
      else{
        setLimit(limit)

      }

    })

  },[refresh,blockchain.account,maxPerWallet])


  useEffect(()=>{
    if(blockchain.account===null)return '';

    const whiteListAddressesLeaves = whiteListAddresses.map((x) => keccak256(x));
    const merkletree = new MerkleTree(whiteListAddressesLeaves, keccak256 ,{sortPairs:true})
    const merklePoof=merkletree.getHexProof(keccak256(blockchain.account));
    const rootNode=merkletree.getRoot().toString('hex');
    let whitelisted=merkletree.verify(merklePoof,keccak256(blockchain.account),rootNode)

    //we check for totalSupply to check if the values have been retrieved
    if(whitelisted===false&&totalSupply!==''){
      setAddressWhitelisted(false)
    }
    else if(totalSupply!==''){
      setAddressWhitelisted(true)
    }
  },[blockchain.account,totalSupply])

  const decrementMintAmount = () => {
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > limit) {
      newMintAmount = limit;
    }
    setMintAmount(newMintAmount);
  };
  const setAmountInput = (amount) => {
    amount = Number(amount)
    if (isNaN(amount)) return;
    if (amount > limit) {
      setMintAmount(limit)
    }
    else {
      setMintAmount(amount)
    }
  }
  useEffect(()=>{
    connectWallet()
  },[])

  console.log(limit)
  return (
    <div className='mint-section'>
     

     
      {/* <div className='MintSection-inner'> 
        {totalSupply!==''&&(
          <div className='family-grand text-green mint-amount-left font-l'>
            {CONFIG.MAX_SUPPLY-totalSupply} <span className='text-white'>Left</span>
          </div>
        )}
        {blockchain.account&&
          <div className='connected-address' onClick={()=>{window.open(`https://etherscan.io/address/${blockchain.account}`)}}>
            <div className='connected-wallet-address'>
              <img src={walletImg}/>
            </div>
            
            <div>
              <p className='font-s text-white family-bold'>CONNECTED AS</p>
              <p className='font-xs text-green family-regular'>{blockchain?.account?.substring(0,8)}...{blockchain?.account?.substring(blockchain.account.length-8,blockchain.account.length)}</p>
            </div>

          </div>
        }
        
        <div className='MintSection-left'>
          <img src={cubImg}/>
        </div>
        <div className='MintSection-right'> 
          <span className='text-center family-grand-heavy font-xlm text-white'>C.U.B. NFT MINT</span>
          <img className='mobile-img' src={cubImg} />

          <div className='mt-2 MintSection-right-bottom'>
            <div className=' text-white font-xs family-grand-heavy'>
            </div>
            <div className='text-white font-xs family-grand-heavy'>
            </div>
            <div className='text-white font-xs family-grand-heavy'>
              PUBLIC MINT PRICE: <span className='text-green'>0.08ETH</span>
            </div>
            <Carousel/>
            <div className='mint-div'>
              {paused===true&&hours!==''?
                <p className='font-m family-grand-bold'>{days}d:  {hours}h:  {minutes}m:  {seconds}s</p>
                :WLActive===true&&paused===false?
                <p className='font-m family-grand-bold'>WL SALE: Free</p>
                :OGActive===true&&paused===false?
                <p className='font-m family-grand-bold'>OG SALE: LIVE</p>
                :publicActive&&paused===false?
                <p className='font-m family-grand-bold'>PUBLIC SALE: LIVE</p>
                :<div style={{flex:window.innerWidth<600?'':1,height:window.innerWidth<600?'30%':''}}></div>
              }
              {
                (blockchain.smartContract===null||blockchain.account===null)&&
                (<button onClick={()=>connectWallet()} className='font-xs connect-button text-white family-grand-heavy'>
                  <div className='connect-icon'>
                    <img src={walletImg}/>
                  </div>
                  Connect Wallet
                </button>)
              }
              {
                blockchain.smartContract!==null&&cost!==''&&claimingNft===false&&paused===false?(
                <div className='mint-div-right'>
                
                  <button onClick={()=>mint()} className='font-xs mint-button text-white family-grand-heavy'>
                    MINT NOW
                  </button>
                  
                  <div className='mint-amount' onClick={()=>openDropdown()}>
                    <span style={{marginTop:'3px'}} className='text-white font-m family-grand-heavy'>{mintAmount}</span> 
                    <img src={downImg}/>

                    <div id='mint-amount-dropdown' className='mint-amount-dropdown'>
                      {Array.from(Array(Number(publicActive?1:limit)).keys()).map((i,index)=>{
                       
                        return(
                          <div onClick={()=>{closeDropdown(index+1)}} key={index} style={{background:index+1===mintAmount?'#AFFF00':''}}>
                            <span style={{color:index+1===mintAmount?'black':''}} className='font-s text-white bold family-bold'>
                              {index +1}
                            </span>
                          </div>
                        )
                      })}
                      
                      
                    </div>
                  </div>
                </div>)
                // :addressWhitelisted===false?
                // // <span className='text-red font-m family-bold'>Not Whitelisted!</span>
                :<img src={loading}/>
              }
            </div>
          </div>
        </div>
      </div> */}
     

     <div className='mint-section-inner'>
        <div className='mint-section-left'>
          <span className='font-m text-gold weight-bold'>PRE-SALE MINT</span>
          {blockchain.account&&
          <span className='font-xs text-white'>{totalSupply} Lucky Apes Minted</span>}
          {/* <div className='info-wrapper'>CONNECTED AS:</div> */}
          {blockchain.account&&
            <div className='font-s info-wrapper text-white'>
              <span className='weight-light'>Connect AS</span>
              <span className='weight-bold'>{blockchain.account.substring(0, 11)}...</span>
            </div>
          }
          <div className='amount-wrapper text-white'>
            
            <span className='weight-light font-s'>AMOUNT</span>
            <div className='amount-wrapper-inner '>
              <button onClick={()=>{decrementMintAmount()}} className='amount-btn'>
                <span  className='font-ms'>
                  -
                </span>
              </button>
              <input className='font-s text-white' value={mintAmount} onChange={(e)=>{setAmountInput(e.target.value)}}/>
              <button onClick={()=>{incrementMintAmount()}} className='amount-btn'>
                <span  className='font-ms'>
                  +
                </span>
              </button>
              <button onClick={()=>{setMintAmount(limit)}} className='max-btn font-s'>MAX</button>
            </div>
           
          </div>
          <div className='font-s info-wrapper text-white'>
            <span className='weight-light'>TOTAL</span>
            {cost&&<span className='weight-bold'>{(cost/1000000000000000000)*mintAmount}ETH</span>}
          </div>
          {
            (blockchain.smartContract===null||blockchain.account===null)&&
            (<button onClick={()=>connectWallet()} className='mint-button font-xs'>
              Connect Wallet
            </button>)
          }
          {
            blockchain.smartContract!==null&&addressWhitelisted===true?(
                
              <button onClick={()=>claimNFTs()} className='font-xs mint-button family-grand-heavy'>
                  {claimingNft===true?(<img src={loading}/>):'MINT NOW'}
              </button>

            )
            :
              <span className='text-red font-m family-bold'>
                {blockchain.account!==null&&'Not Whitelisted!'}
              </span>
            
          }
        </div>
        <Cube/>
     </div>
    </div>
  )
}

export default MintSection
